import React from "react";

import "./CitySectionFour.css";

export default function CitySectionFour(prop) {
  return (
    <div className="city-section-four-main-container" id="video-animation">

      <div className="city-section-four-main-container-circle-in-the-middle"></div>
      
      <div className="city-section-four-main-container-sub-container-one">
        <div className="city-section-four-main-container-sub-container-one-heading">
          <h2>
            Video Editing /<br />
            Animation
          </h2>
        </div>

        <div className="city-section-four-main-container-sub-container-one-paragraph">
          <p>
            {prop.paragraph}
          </p>
        </div>

        <div className="city-section-four-main-container-sub-container-one-button">
          <button>Learn More</button>
        </div>
      </div>

      <div className="city-section-four-main-container-sub-container-two">
        <img src="https://res.cloudinary.com/dx3u24xir/image/upload/f_auto,q_auto/v1692782094/City/city-section-four/pic2_19_to6nkh.webp" alt="Digital Marketing In The U.S."/>
      </div>
    </div>
  );
}
